.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #0a1b6cc7;
  color: #fff;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding-top: 80px;
}

.header .logo h1 {
  margin: 0;
  font-size: 1.5rem;
}

.header .nav-socials {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.header .nav-social-container {
  display: flex;
  align-items: center;
}

.header nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
  gap: 10px;
}

.header nav ul li {
  margin:0 30px 0 20px;
  display: flex;
}

.header nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
}

.header nav ul li a:hover {
  color: #9a9a9ecd;
}

.header .socials {
  display: flex;
  align-items: center;
  margin-left: 15px;
}

.header .socials a {
  margin-left: 10px;
}

.header .socials img {
  width: 25px;
  height: 25px;
}
