.carrousel{display:flex;justify-content:center;align-items:center;position:relative;width:100%;margin:0 auto}.carrousel-btn{position:absolute;top:50%;transform:translateY(-50%);background-color:#000;color:#fff;border:2px solid gray;padding:20px 10px;cursor:pointer;z-index:10}.carrousel-btn.prev{left:0;border-radius:50% 10% 10% 50%}.carrousel-btn.prev::after{content:"←"}.carrousel-btn.next{right:0;border-radius:10% 50% 50% 10%}.carrousel-btn.next::after{content:"→"}.carrousel-images{display:flex;align-items:center;position:relative;width:95%;justify-content:center}.carrousel-card{flex:0 0 33%;transition:transform .5s ease,z-index .5s ease;position:relative;z-index:1}.carrousel-card img{width:100%;height:100%;object-fit:cover;border-radius:10px;box-shadow:0 5px 15px rgba(0,0,0,.2)}h3{color:#f8f8ff}p{color:#f8f8ff}.prev-card{flex:0 0 10%;transform:scale(1);z-index:0}.current-card{flex:0 0 75%;transform:scale(1.1);z-index:2}.current-card:hover .overlay{opacity:1}.current-card .overlay{position:absolute;top:-19px;left:-19px;width:100%;height:100%;background:rgba(0,0,0,.9);color:#fff;opacity:0;transition:opacity .3s ease;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;padding:20px;border-radius:10px}.current-card .overlay h3{margin-bottom:10px;font-size:1.5rem}.current-card .overlay p{font-size:1rem;margin-bottom:8px}.current-card .overlay a{margin-top:10px;color:#ffdb58;font-weight:bold;text-decoration:none;transition:color .3s ease}.current-card .overlay a:hover{color:#fb0}.next-card{flex:0 0 10%;transform:scale(1);z-index:0}.carrousel-btn:hover{background-color:rgba(0,0,0,.7)}@media(max-width: 767px){.carrousel .current-card .overlay{top:0px;width:100%;height:250%;background:rgba(0,0,0,.9);transition:opacity .3s ease;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;padding:20px;border-radius:10px;position:relative}.carrousel{display:flex;flex-direction:column;align-items:center;position:relative}.carrousel-btn{position:absolute;top:30%;transform:translateY(-50%);background-color:#000;color:#fff;padding:20px 10px;cursor:pointer;z-index:10}.carrousel-btn.prev{left:-20px;border-radius:50% 10% 10% 50%}.carrousel-btn.prev::after{content:"←"}.carrousel-btn.next{right:-20px;border-radius:10% 50% 50% 10%}.carrousel-btn.next::after{content:"→"}.carrousel-images{display:flex;flex-direction:column;align-items:center;width:100%}.carrousel-card{flex:0 0 auto;width:80%;margin:10px 0;transition:transform .5s ease,z-index .5s ease}.carrousel-card img{width:100%;border-radius:10px;box-shadow:0 5px 15px rgba(0,0,0,.2);cursor:pointer}.carrousel-card .current-card{flex:0 0 75%;transform:scale(1.1);z-index:2}.carrousel-card.prev-card,.carrousel-card.next-card{visibility:hidden}.description{display:none;margin-top:20px;text-align:center;background-color:rgba(0,0,0,.9);color:#fff;padding:15px;border-radius:10px;width:80%}.description.active{display:block}}